*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background: #414181;
  color: white;
}
body::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#f00, #f0f);
  clip-path: circle(30% at right 70%);
}
.container{
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  max-width: 1200px;
  flex-wrap: wrap;
  z-index: 1;
}
.container .card{
  position: relative;
  width: 280px;
  height: 400px;
  margin: 30px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 1rem;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
}
main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));
  padding: 1.5rem;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 1rem;
  z-index: 11;
  appearance: none;
  background: none;
  outline: none;
  border: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0 0 16px 16px;
  margin-top: -1.5rem;
  box-shadow: 0 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 1.25rem;
}
.search-bar .search-bar:focus{
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location{
  color: #fff;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  margin-top: 1rem;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}
.location-box .date{
  color: #fff;
  font-size: 1.25rem;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}
.wheater-box{
  text-align: center;
}
.wheather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 1rem;

  padding: 1rem 1.5rem;
  color: #fff;
  font-size: 6rem;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
}
.wheather-box .wheather{
  color: #fff;
  font-size: 4rem;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}